.app-wrapper {
    /*margin-left: 10%;*/
    /*margin-right: 20%;*/
    margin: 0 auto;
    display: grid;
    width: 1200px;
    grid-template-areas: "h h" "n c";
    grid-template-rows: 70px 1fr;
    grid-template-columns: 200px 10fr;
    /*grid-gap: 10px;*/
}

.app-wrapper-content {
    grid-area: c;
    background-color: sandybrown;
}




